body, html {
    height: 100%;
    margin: 0;
  }
  
.dashboard-container {
  display: flex;
  height: 100vh;
}

.dashboard-content {
  flex: 1;
  padding: 20px;
  background-color: #ffffff;
}

.dashboard-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #6F4E37;
  margin-bottom: 20px;
  text-align: center;
}

.dashboard-cards {
  gap: 20px;
  margin-bottom: 20px;
}
.admin-card {
  background: #fff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  flex: 1;
  margin-bottom: 60px;
  margin-top: 20px;
  width: 222px;
  height: 227px;
}
.dashboard-card {
  background: #fff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 60px;
  margin-top: 20px;
  width: 222px;
  height: 136px;
}
.subContainer{
    display: flex;
    gap: 260px;
    padding-left: 240px;
    padding-top: 110px;
}
.admin-info {
  display: flex;
  gap: 20px; 
}
.list-email,
.list-name {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 9px;
}
.tooltip {
  position: absolute;
  background-color: #333;
  color: #fff;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 14px;
  display: none;
  white-space: nowrap;
  z-index: 1000;
}
.list-email li,
.list-name li {
  margin-bottom: 8px; 
}
.list-email li {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 130px; 
  cursor: pointer;
}
.highlight {
  font-size: 65px;
  color: #FC7E2F;
  font-weight: bold;
}
.text {
  font-size: 17px;
  font-weight: bold;
}
.admin-text {
  text-align: center;
  font-weight: bold;
  font-size: 17px;
}
.dashboard-chart {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-top: 20px;
  width: 555px;
  height: 613px;
}

.textContainer {
  display: flex;
  align-items: center; 
  gap: 20px;
  margin-top: -65px;
  padding-left: 15px;
}
.textContainer2 {
  display: flex;
  align-items: center; 
  gap: 30px;
  margin-top: -65px;
  padding-left: 35px;
}
h3 {
  position: relative;
  color: #243465;
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  margin: 10px 0;
}

h3:after {
  content: ""; 
  display: block;
  width: 100%; 
  height: 1.5px;
  background-color: #898A8D; 
  margin: 8px auto 0;
}

